import {Stitch, UserPasswordAuthProviderClient} from "mongodb-stitch-browser-sdk";
import React, { Component } from 'react';

const APP_ID = "gather-your-party-qnzcm";
const stitchClient = Stitch.initializeDefaultAppClient(APP_ID);

const emailPasswordClient = stitchClient.auth
  .getProviderClient(UserPasswordAuthProviderClient.factory, "userpass");



class ConfirmEmail extends Component {
  constructor(props){
    super(props);
    this.state = {
      message: ""
    }
  }

  // Fetch the list on first mount
  componentDidMount() {
    this.setLoginMessage();
  }

  render() {
    const { message } = this.state;
    return (
    <p>{message}</p>
    );
  }

  setLoginMessage() {
    const params = new URLSearchParams(window.location.search);
    const token = params.get('token');
    const tokenId = params.get('tokenId');
    emailPasswordClient
      .confirmUser(token, tokenId)
      .then(() => this.setState({message: "success!"}))
      .catch(err => this.setState({message: 'error\n\n' + err}));
  }

}
export default ConfirmEmail;